"use client"

import { useState } from "react"
import Link from "next/link"
import { Search, SlidersHorizontal } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ProductFilters } from "@/components/features/products/ProductFilters"
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet"
import { ProductCardSkeleton } from "@/components/ui/skeleton"
import { ErrorState } from "@/components/ErrorBoundary"
import { useListings } from "@/lib/hooks/queries/useListings"
import { formatPrice } from "@/lib/utils"

// Mock data fallback (remove when backend is fully connected)
const mockProducts = [
  { id: '1', title: 'Japanese Maple', price: 149.99, images: ['üçÅ'], category: 'Ornamental' },
  { id: '2', title: 'Blue Spruce', price: 89.99, images: ['üå≤'], category: 'Evergreen' },
  { id: '3', title: 'Apple Tree', price: 129.99, images: ['üçé'], category: 'Fruit' },
  { id: '4', title: 'Red Oak', price: 199.99, images: ['üå≥'], category: 'Shade' },
  { id: '5', title: 'Cherry Blossom', price: 179.99, images: ['üå∏'], category: 'Ornamental' },
  { id: '6', title: 'Pine Tree', price: 69.99, images: ['üå≤'], category: 'Evergreen' },
]

export default function TreesPage() {
  const [searchQuery, setSearchQuery] = useState("")

  // Fetch listings from backend
  const { data, isLoading, isError, error, refetch } = useListings({
    status: 'active',
    search: searchQuery || undefined,
  })

  // Use actual data if available, fallback to mock data
  const products = data?.listings || mockProducts
  const usesMockData = !data

  const filteredProducts = searchQuery && usesMockData
    ? products.filter(p => p.title.toLowerCase().includes(searchQuery.toLowerCase()))
    : products

  return (
    <div className="container py-8">
      <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">All Trees</h1>
          <p className="text-muted-foreground">
            Browse our collection of premium trees
            {usesMockData && (
              <span className="ml-2 text-xs text-yellow-600 dark:text-yellow-400">
                (Using mock data - backend not connected)
              </span>
            )}
          </p>
        </div>

        <div className="flex items-center gap-2">
          <div className="relative w-full md:w-[300px]">
            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
            <Input
              type="search"
              placeholder="Search trees..."
              className="pl-9"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
            />
          </div>

          <Sheet>
            <SheetTrigger asChild>
              <Button variant="outline" size="icon" className="md:hidden">
                <SlidersHorizontal className="h-4 w-4" />
              </Button>
            </SheetTrigger>
            <SheetContent side="left">
              <SheetHeader>
                <SheetTitle>Filters</SheetTitle>
              </SheetHeader>
              <div className="mt-4">
                <ProductFilters />
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
        <div className="hidden md:block">
          <ProductFilters />
        </div>

        <div className="md:col-span-3">
          {/* Loading State */}
          {isLoading && (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
              {Array.from({ length: 6 }).map((_, i) => (
                <ProductCardSkeleton key={i} />
              ))}
            </div>
          )}

          {/* Error State */}
          {isError && (
            <ErrorState
              title="Failed to load products"
              message={error?.message || "Unable to fetch product listings"}
              onRetry={() => refetch()}
            />
          )}

          {/* Empty State */}
          {!isLoading && !isError && filteredProducts.length === 0 && (
            <div className="text-center py-12">
              <div className="text-4xl mb-4">üå≥</div>
              <h3 className="text-lg font-medium">No trees found</h3>
              <p className="text-muted-foreground">
                Try adjusting your search or filters
              </p>
            </div>
          )}

          {/* Products Grid */}
          {!isLoading && !isError && filteredProducts.length > 0 && (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredProducts.map((product) => {
                const image = product.images?.[0] || 'üå≥'
                const category = product.category || 'Tree'

                return (
                  <Link
                    key={product.id}
                    href={`/trees/${product.id}`}
                    className="group block bg-card rounded-xl border overflow-hidden hover:shadow-lg transition-all hover:-translate-y-1"
                  >
                    <div className="aspect-square bg-muted flex items-center justify-center text-6xl group-hover:scale-110 transition-transform duration-500">
                      {image}
                    </div>
                    <div className="p-4">
                      <div className="flex items-center justify-between mb-2">
                        <span className="text-xs font-medium px-2 py-1 rounded-full bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400">
                          {category}
                        </span>
                        <span className="text-sm text-muted-foreground flex items-center gap-1">
                          ‚≠ê 4.8
                        </span>
                      </div>
                      <h3 className="font-bold mb-2 group-hover:text-green-600 transition-colors">
                        {product.title}
                      </h3>
                      <div className="font-medium text-lg">
                        {formatPrice(product.price)}
                      </div>
                    </div>
                  </Link>
                )
              })}
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
